import React, { Component } from 'react';
import 'react-dates/initialize';
import { DayPickerRangeController } from 'react-dates';
import moment from 'moment';
import 'react-dates/lib/css/_datepicker.css';
import './style.css';

class DatepickerWrapp extends Component {
    constructor(props) {
        super(props);

        this.state = {
            height: 0,
            headerDay: 0
        };
    }

    componentDidMount() {
        const { daySize } = this.props;

        this.setState({ headerDay: daySize })
    }

    render() {
        const { startDate, endDate, focusedInput, daySize, onDatesChange, onFocusChange, headerHeight, bottomHeight} = this.props;
        let heightWrapp;

        if (headerHeight > 0 && bottomHeight > 0) {
            heightWrapp = window.innerHeight - headerHeight - bottomHeight - 15
        }

        return (
            <div className="Datepicker-wrapp" style={{ height: heightWrapp }}>
                <DayPickerRangeController
                    startDate={startDate}
                    endDate={endDate}
                    onDatesChange={onDatesChange}
                    focusedInput={focusedInput}
                    onFocusChange={onFocusChange}
                    orientation="verticalScrollable"
                    numberOfMonths={12}
                    renderDayContents={(day) => 
                        <div className="CalendarDay__item" style={{height: this.state.headerDay}}>
                            {day.format('D')}
                        </div>}
                    noNavButtons
                    noBorder
                    minimumNights={0}
                    daySize={daySize}
                    isOutsideRange={day => (moment().diff(day) > 0)}
                />
            </div>
        )
    }
}

export default DatepickerWrapp;